div{
  color: red;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
body{
background-color: rgb(32, 32, 51);
display: flex;
align-items: center;
justify-content: center;
}
table{
  margin-top: 15vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.cell{
  
  height: min(15vh,15vw);
  width: min(15vh,15vw);
  font-size:min(15vh,15vw) ;
  border-radius: 10%;
  background-color: #F8EFBA;
  display: flex;
  align-items: center;
  justify-content: center;
  border: black 20px;
}

.number{
  height: 50%;
  width: 50%;
  text-shadow: black 2px;
  font-size: 37%;
  color: azure;
  font-family: cursive;
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 2px 0 0 rgb(22, 22, 22), -2px 0 0 rgb(15, 15, 15), 0 2px 0 rgb(17, 17, 17), 0 -2px 0 rgb(8, 8, 8), 1px 1px rgb(17, 17, 17), -1px -1px 0 rgb(10, 10, 10), 1px -1px 0 #fff, -1px 1px 0 #fff;
}


